import React from "react";
import { Form, Label, Input } from "./AuthForm.styled";

function AuthForm() {
  return (
    <>
      <Form>
        <Label> : </Label>
        <Input placeholder="your@mail.com" />
        <Label>: </Label>
        <Input placeholder="" />
      </Form>
    </>
  );
}
export default AuthForm;
