import { Route, Routes, Link } from "react-router-dom";

export default function CategoryToReport() {
  return (
    <>
      <div>
        <button>
          <Link to="1"></Link>
        </button>
        <button>
          <Link to="2"></Link>
        </button>
        <button>
          <Link to="3"></Link>
        </button>
        <button>
          <Link to="4"></Link>
        </button>
        <button>
          <Link to="5"></Link>
        </button>
        <button>
          <Link to="6">  </Link>
        </button>
        <button>
          <Link to="7"></Link>
        </button>
        <button>
          <Link to="8">, </Link>
        </button>
        <button>
          <Link to="9">, </Link>
        </button>
        <button>
          <Link to="10"></Link>
        </button>
        <button>
          <Link to="11"></Link>
        </button>
      </div>
      <Routes>
        <Route path="1" element={<h1></h1>} />
        <Route path="2" element={<h1></h1>} />
        <Route path="3" element={<h1></h1>} />
        <Route path="4" element={<h1></h1>} />
        <Route path="5" element={<h1></h1>} />
        <Route path="6" element={<h1>  </h1>} />
        <Route path="7" element={<h1></h1>} />
        <Route path="8" element={<h1> </h1>} />
        <Route path="9" element={<h1>, </h1>} />
        <Route path="10" element={<h1></h1>} />
        <Route path="11" element={<h1></h1>} />
      </Routes>
    </>
  );
}